@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100..900&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: inherit;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: normal;
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

body {
  line-height: 1;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

picture,
video,
canvas,
svg {
  max-width: 100%;
  display: block;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
}

body {
  background-color: #121212;
  color: #e3e3e3;
  font-family: "Roboto", sans-serif;
}

.top-menu {
  height: 80px;
  border-bottom: 2px solid #272727;
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
}
.top-menu .login {
  display: flex;
  align-items: center;
  gap: 10px;
}
.top-menu .search {
  display: flex;
  align-items: center;
}
.top-menu .search input {
  background-color: #272727;
  border: 2px solid #272727;
  height: 40px;
  width: 250px;
  font-size: 14px;
  padding: 0 20px 0 20px;
  border-radius: 20px;
  outline: none;
  color: #fff;
  background-image: url("../assets/icons/search.svg");
  background-repeat: no-repeat;
  background-size: 20px;
  background-position: center right 10px;
}
.top-menu .search input::placeholder {
  color: #fff;
  opacity: 0.5;
}
.top-menu .search input:focus {
  border: 2px solid #e9ff70;
}
.top-menu .nav-logo {
  display: flex;
  align-items: center;
  gap: 10px;
}
.top-menu .nav-logo nav ul {
  display: flex;
  align-items: center;
  gap: 10px;
}
.top-menu .nav-logo nav ul li a {
  color: #e3e3e3;
  text-decoration: none;
}
.top-menu .nav-logo nav ul li a:hover {
  color: #e9ff70;
}